<template>
  <h3 class="col s-12">
    <span
      :class="isData ? 'enable' : 'disable'"
      @click="isShow = !isShow && isData"
    >
      <i class="ri-line-chart-line"></i> Biểu đồ
    </span>
  </h3>

  <div class="chart-wrapper" v-if="isShow">
    <div class="button-wrapper">
      <span
        title="Nhiệt độ"
        @click="
          isClick.temp
            ? null
            : (isClick = { temp: true, rain: false, aqi: false })
        "
      >
        <i
          class="ri-temp-hot-line"
          :class="isClick.temp ? 'selected' : 'unselected'"
        ></i>
      </span>

      <span
        title="Tỉ lệ mưa"
        @click="
          isClick.rain
            ? null
            : (isClick = { temp: false, rain: true, aqi: false })
        "
      >
        <i
          class="ri-rainy-line"
          :class="isClick.rain ? 'selected' : 'unselected'"
        ></i>
      </span>

      <span
        title="Chất lượng không khí"
        @click="
          isClick.aqi
            ? null
            : (isClick = { temp: false, rain: false, aqi: true })
        "
      >
        <i
          class="ri-flask-line"
          :class="isClick.aqi ? 'selected' : 'unselected'"
        ></i>
      </span>
    </div>
    <VueHighcharts :options="chartOptions" />
  </div>
</template>
<script>
import VueHighcharts from "vue3-highcharts";
import Highcharts from "highcharts";
import exportingInit from "highcharts/modules/exporting";
import exportingInit2 from "highcharts/modules/export-data";
exportingInit(Highcharts);
exportingInit2(Highcharts);
import exportingInit3 from "highcharts/modules/drilldown";
import exportingInit4 from "highcharts/modules/accessibility";
exportingInit3(Highcharts);
exportingInit4(Highcharts);
let colorArr = [];
Highcharts.setOptions({
  colors: colorArr,
});

export default {
  components: {
    VueHighcharts,
  },

  props: {
    isData: Boolean,
    hourly: Array,
    hourlyAir: Array,
  },

  data: () => ({
    color: {
      temp: "#FFCC99",
      rain: "#99CCFF",
    },
    tempArr: [],
    rainArr: [],
    drilldown: [],
    timeArr: [],
    aqiArr: [],
    isShow: false,
    isClick: {
      temp: true,
      rain: false,
      aqi: false,
    },
    chartOptions: {
      series: [
        {
          name: "",
          colorByPoint: true,
          data: [],
          fillColor: "",
        },
      ],
      chart: {
        type: "",
        zoomType: "xy",
        scrollablePlotArea: {
          minWidth: 2000,
          scrollPositionX: 0.5,
        },
      },
      title: {
        text: "",
      },
      xAxis: {
        title: {
          text: "Thời gian",
        },
        type: "category",
      },
      yAxis: {
        title: {
          text: "",
        },
      },
      tooltip: {
        valueSuffix: "",
      },
      legend: {
        enabled: false,
      },
      drilldown: {
        drillUpButton: {
          relativeTo: "plotBox",
        },
        activeAxisLabelStyle: {
          textDecoration: "none",
        },
        activeDataLabelStyle: {
          textDecoration: "none",
        },
        series: [],
      },
    },
  }),
  methods: {
    handleTempClick() {
      console.log("click temp");
      colorArr.length = 0;
      colorArr.push(this.color.temp);
      this.chartOptions.series[0].name = "Nhiệt độ (℃)";
      this.chartOptions.series[0].data = this.tempArr;
      this.chartOptions.series[0].fillColor = this.color.temp;
      this.chartOptions.chart.type = "area";
      this.chartOptions.title.text = "Dự báo nhiệt độ";
      this.chartOptions.xAxis.categories = this.timeArr;
      this.chartOptions.yAxis.title.text = "Đơn vị (℃)";
      this.chartOptions.tooltip.valueSuffix = "℃";
    },
    handleRainClick() {
      console.log("click rain");
      colorArr.length = 0;
      colorArr.push(this.color.rain);
      this.chartOptions.series[0].name = "Lượng mưa (mm)";
      this.chartOptions.series[0].data = this.rainArr;
      this.chartOptions.series[0].fillColor = this.color.rain;
      this.chartOptions.chart.type = "areaspline";
      this.chartOptions.title.text = "Dự báo mưa";
      this.chartOptions.xAxis.categories = this.timeArr;
      this.chartOptions.yAxis.title.text = "Đơn vị (mm)";
      this.chartOptions.tooltip.valueSuffix = "mm";
    },
    handleAqiClick() {
      console.log("click aqi");
      colorArr.length = 0;
      this.hourlyAir.forEach((ele) => {
        colorArr.push(ele.indexes.baqi.color);
        console.log(colorArr);
      });
      this.chartOptions.series[0].name = "AQI";
      this.chartOptions.series[0].data = this.aqiArr;
      this.chartOptions.chart.type = "column";
      this.chartOptions.title.text = "Dự báo chất lượng không khí";
      this.chartOptions.xAxis.categories = null;
      this.chartOptions.yAxis.title.text = "Chỉ số AQI";
      this.chartOptions.tooltip.valueSuffix = null;
      this.chartOptions.drilldown.series = this.drilldown;
    },

    updateData() {
      this.drilldown.length = 0;
      this.timeArr.length = 0;
      this.tempArr.length = 0;
      this.rainArr.length = 0;
      this.aqiArr.length = 0;
      for (let i = 0; i < 96; ++i) {
        let ele = this.hourly[i];
        let day = new Date(ele.dt * 1000);
        let date = day.getDate();
        date =
          date == 1
            ? date + "st"
            : date == 2
            ? date + "nd"
            : date == 3
            ? date + "rd"
            : date + "th";
        let hour = day.getHours();
        hour =
          hour == 0
            ? "12AM"
            : hour == 12
            ? "12PM"
            : hour > 12
            ? hour - 12 + "PM"
            : hour + "AM";
        day=`${date} ${hour}`
        this.timeArr.push(day);

        // set data
        // console.log("update temp");
        this.tempArr.push(ele.main.temp);

        // console.log("update rain");
        try {
          this.rainArr.push(ele.rain["1h"]);
        } catch {
          this.rainArr.push(0);
        }

        // console.log("update aqi");
        try {
          ele = this.hourlyAir[i];
          // console.log(ele);
          this.aqiArr.push({
            name: day,
            y: ele.indexes.baqi.aqi,
            drilldown: day,
          });
          this.drilldown.push({
            name: "Giá trị",
            id: day,
            data: [
              ["CO (ppb)", ele.pollutants.co.concentration.value],
              ["NO2 (ppb)", ele.pollutants.no2.concentration.value],
              ["O3 (ppb)", ele.pollutants.o3.concentration.value],
              ["PM10 (ug/m3)", ele.pollutants.pm10.concentration.value],
              ["PM2.5 (ug/m3)", ele.pollutants.pm25.concentration.value],
              ["SO2 (ppb)", ele.pollutants.so2.concentration.value],
            ],
          });
        } catch {
          console.log(this.aqiArr, this.timeArrAir);
        }
      }
    },
    showData() {
      this.isClick.temp
        ? this.handleTempClick()
        : this.isClick.rain
        ? this.handleRainClick()
        : this.handleAqiClick();
    },
    hideTable() {
      console.log("hide");
      const table = document.getElementsByClassName("highcharts-data-table")[0];
      try {
        table.style.display = "none";
      } catch (e) {
        return;
      }
    },
  },
  watch: {
    hourly() {
      this.updateData();
      this.showData();
    },
    isClick() {
      this.showData();
    },
    isShow() {
      this.isShow ? this.showData() : this.hideTable();
    },
  },
};
</script>
<style>
.chart-wrapper {
  position: relative;
  width: 100%;
}

.button-wrapper {
  background-color: #fff;
  display: flex;
  color: #333;
  justify-content: center;
  font-size: 2rem;
}
.button-wrapper span {
  margin: 1rem;
}
.button-wrapper span:hover i {
  background-color: rgb(230, 235, 245);
  cursor: pointer;
}
.button-wrapper span i {
  padding: 10px;
}
.selected {
  background-color: rgb(230, 235, 245) !important;
}
.unselected {
  background-color: rgb(247, 247, 247);
}

.highcharts-data-table {
  color: #333;
}
.highcharts-table-caption {
  background-color: #333;
  color: #fff;
}
.highcharts-data-table table {
  border-collapse: collapse;
  border-spacing: 0;
  background: white;
  min-width: 100%;
  margin-top: 10px;
  font-family: sans-serif;
  font-size: 0.9em;
}
.highcharts-data-table td,
.highcharts-data-table th,
.highcharts-data-table caption {
  border: 1px solid silver;
  padding: 0.5em;
  /* background-color: #333; */
}
.highcharts-data-table tr:nth-child(even),
.highcharts-data-table thead tr {
  background: #f8f8f8;
}
.highcharts-data-table tr:hover {
  background: #eff;
}
.highcharts-data-table caption {
  border-bottom: none;
  font-size: 1.1em;
  font-weight: bold;
}

/* -------------------------------------------------------------------- */
</style>
