<template>
  <h3 class="col s-12">
    <span
      :class="isData ? 'enable' : 'disable'"
    >
      <i class="ri-radar-line"></i> Dự báo theo ngày
    </span>
  </h3>
  <ul class="bottom-daily col l-2 m-12 s-12" v-for="day in days" :key="day">
    <li class="bottom-daily-list">
      <p class="daily-date">{{ day.date }}</p>
      <img class="daily-icon" :src="day.icon" alt="icon" />
      <div class="daily-rain">
        <span> <i class="ri-rainy-fill"></i>{{ day.rain }}% </span>
      </div>
      <div class="daily-temp">
        <span><i class="ri-temp-hot-line"></i>{{ day.temp_max }}</span>
        <span><i class="ri-temp-cold-line"></i>{{ day.temp_min }}</span>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    isData: Boolean,
    daily: Array,
  },
  data: () => ({
    isForecast: false,
    days: [],
  }),
  watch: {
    daily: function () {
      this.days = this.daily.map((ele) => ({
        date: new Date(ele.dt * 1000).toDateString().slice(0, -5),
        icon: `http://openweathermap.org/img/wn/${ele.weather[0].icon}.png`,
        rain: Math.round(ele.pop * 100),
        temp_max: Math.round(ele.temp.max),
        temp_min: Math.round(ele.temp.min),
      }));
    },
  },
};
</script>

