import axios from "axios";

const api = {
  weather: '1f9a804ac3414d83d0a01f8ccd003ead',
  air: '5614c1694ae34c008a8bcbbfc22cc7df',    //tienVMU
  // air:'a5233e6c80a74bcaa76aec1c60894242',     //duanVMU
  // air: '3bfe0f319d4e40e1bcfd7a255de903a0',    //maiVMU
  // air: '016b944e582c4aa58d9ee6a29c522193',    //mai
  location: 'pk.eyJ1IjoidHNoYTEyMyIsImEiOiJja213MjljbGIwYXE2Mm5uOGlvOGlna3p0In0.w2WOSydwrJA7nNTUdPGLDQ'
}
const baseUrl = {
  hourly: 'https://pro.openweathermap.org/data/2.5/forecast/hourly',
  location: 'https://api.mapbox.com/geocoding/v5/mapbox.places',
  weather: 'https://pro.openweathermap.org/data/2.5/onecall',
  // chart: 'http://history.openweathermap.org/data/2.5/history/accumulated_precipitation?lat=20.84293378459086&lon=106.71540763655682&start=1586853378&end=1589445367&appid=1f9a804ac3414d83d0a01f8ccd003ead',
  air: 'https://api.breezometer.com/air-quality/v2/current-conditions',
  airHourly: 'https://api.breezometer.com/air-quality/v2/forecast/hourly'
}

export const getWeather = async (lon, lat) => {
  const hourly = `${baseUrl.hourly}?lat=${lat}&lon=${lon}&appid=${api.weather}&lang=vi&units=metric`
  const location = `${baseUrl.location}/${lon},${lat}.json?types=locality&access_token=${api.location}`
  const weather = `${baseUrl.weather}?lat=${lat}&lon=${lon}&appid=${api.weather}&units=metric&exclude=minutely&lang=vi`
  const air = `${baseUrl.air}?lat=${lat}&lon=${lon}&key=${api.air}&features=breezometer_aqi,pollutants_concentrations,pollutants_aqi_information`
  /*FIXME:*/ const airHourly = `${baseUrl.airHourly}?lat=${lat}&lon=${lon}&key=${api.air}&hours=96&features=breezometer_aqi,pollutants_concentrations,pollutants_aqi_information`
  return await Promise.all([
    await axios.get(location),  //mapbox
    await axios.get(weather),   //openweather onecall
    await axios.get(air),       //breezometer current
    await axios.get(hourly),    //openweather hourly
    await axios.get(airHourly)  //breezometer hourly
  ])
}

export const getCoordinate = async (keyWord) => {
  const link = `${baseUrl.location}/${keyWord}.json?types=country,region,district,place,locality&access_token=${api.location}`
  return await axios.get(link)
}

export const getAccumulated = async (lon, lat) => {
  const start = ''
  const end = ''
  const temperature = `${baseUrl}_temperature?lat=${lat}&lon=${lon}&appid=${api.weather}&start=${start}&end=${end}`
  const precipitation = `${baseUrl}_precipitation?lat=${lat}&lon=${lon}&appid=${api.weather}&start=${start}&end=${end}`
  return await Promise.all([await axios.get(temperature), await axios.get(precipitation)])
}