<template>
  <div class="search-bar" @keyup.esc="isShow = false">
    <!-- Show location -->
    <div class="search-location" @click="isShow = !isShow">
      <i class="ri-map-pin-line search-location-icon"></i>
      <span class="search-location-name">
        {{ name }}
      </span>
    </div>
    <!-- End Show location -->

    <!-- Show Search box -->
    <div class="search-box" v-if="isShow">
      <!-- Input -->
      <div class="input-wrap">
        <i class="ri-search-line search-box-icon"></i>
        <input
          name="input"
          type="text"
          placeholder="Nhập địa điểm..."
          v-model="keyWord"
          autocomplete="off"
          @keyup.enter="handleSearchLocation"
        />
      </div>
      <!-- End Input -->

      <!-- Current location -->
      <div class="search-list">
        <div class="search-list-item" @click="handleCurrentLocation">
          <i class="ri-focus-3-line search-list-icon"></i>
          <span>Vị trí hiện tại</span>
        </div>
        <!-- End Current location -->
        <!-- List Location -->
        <ul v-for="location in locations" :key="location">
          <li
            class="search-list-item"
            @click="
              handleSelectLocation(
                location.center[0],
                location.center[1],
                location.place_name
              )
            "
          >
            <p class="ri-map-pin-line search-list-icon"></p>
            <span>{{ location.place_name }}</span>
          </li>
        </ul>
        <!-- End List location -->
      </div>
    </div>
    <!-- End Show search box -->
  </div>
  <div id="modal" v-if="isShow" @click="isShow = false"></div>
</template>

<script>
import { getCoordinate, getWeather } from "../data.js";
export default {
  data: () => ({
    name: "",
    isShow: false,
    keyWord: "",
    locations: [],
  }),
  props: {
    weather: Object,
    setData: Function,
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    getAndSet(lon, lat, place_name) {
      getWeather(lon, lat).then((res) => {
        this.name = place_name || res[0].data.features[0].place_name;
        this.setData([res[1].data, res[2].data, res[3].data, res[4].data]);
      });
    },
    resetData() {
      this.keyWord = "";
      this.locations = [];
      this.isShow = false;
    },
    handleSearchLocation() {
      getCoordinate(this.keyWord).then(
        (res) => (this.locations = res.data.features)
      );
    },
    handleCurrentLocation() {
      this.resetData();
      navigator.geolocation.getCurrentPosition((res) =>
        this.getAndSet(res.coords.longitude, res.coords.latitude, "")
      );
    },
    handleSelectLocation(lon, lat, place_name) {
      this.resetData();
      this.getAndSet(lon, lat, place_name);
    },
  },
};
</script>

<style scoped>
.search-bar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  position: relative;
  z-index: 99;
}
.search-location:hover{
  background-color: #eee;
  border-radius: 1rem;
}
.search-location {
  display: flex;
  align-items: center;
}

.search-location-name {
  padding-left: 8px;
  cursor: pointer;
  text-align: left;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.search-location-icon {
  font-size: 1.3rem;
  cursor: pointer;
}

.refresh-icon-active {
  animation: rotate 750ms ease-out infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.search-box {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  padding: 10px 0;
  border-radius: 10px;
  color: #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  background-color: #222;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.input-wrap {
  display: flex;
  align-items: center;
  padding: 0 10px;
}

[name="input"] {
  flex: 1;
  padding: 10px;
  font-size: 1rem;
  background-color: transparent;
  color: #ccc;
  border: none;
  outline: none;
  -webkit-appearance: none;
}

.loading .loading-icon {
  border: 0.2rem solid grey;
  width: 2rem;
  height: 2rem;
  padding: 12px;
  border-top-color: white;
  border-radius: 50%;
  animation: spinner 1s linear 0s infinite;
}

@keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.search-list {
  text-align: left;
}

.search-list-item {
  display: flex;
  align-items: center;
  list-style: none;
  cursor: pointer;
  padding: 10px;
}

.search-list-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.search-list-icon {
  padding-right: 10px;
}

#modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(39, 39, 39, 0.5);
  z-index: 10;
}
</style>
