<template>
  <div>
    <div icon="sunny" v-if="clearSky">
      <span class="sun"></span>
    </div>

    <div icon="snowy" v-if="haze"></div>
    <div icon="snowy" v-if="snowy">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>

    <div icon="stormy" v-if="stormy">
      <span class="cloud"></span>
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>

    <div icon="cloudy" v-if="cloudy">
      <span class="cloud"></span>
      <span class="cloud"></span>
    </div>

    <div icon="nightmoon" v-if="cloudyNight">
      <span class="moon"></span>
      <span class="cloud"></span>
      <span class="cloud"></span>
      <span class="meteor"></span>
    </div>
    <div icon="nightmoon" v-if="clearNight">
      <span class="moon"></span>
      <span class="meteor"></span>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    cloudy: false,
    cloudyNight: false,
    clearSky: false,
    clearNight: false,
    snowy: false,
    haze: false,
    stormy: false,
  }),
  props: {
    animation: Object,
  },
  watch: {
    animation: function () {
      this.cloudy =
        this.animation.isDay &&
        800 < this.animation.id &&
        this.animation.id < 805;
      this.cloudyNight =
        !this.animation.isDay &&
        800 < this.animation.id &&
        this.animation.id < 805;
      this.clearSky = this.animation.isDay && this.animation.id === 800;
      this.clearNight = !this.animation.isDay && this.animation.id === 800;
      this.snowy = 599 < this.animation.id && this.animation.id < 623;
      this.haze = 700 < this.animation.id && this.animation.id < 782;
      this.stormy = 199 < this.animation.id && this.animation.id < 532;
    },
  },
};
</script>