<template>
  <div class="card-top row">
    <div class="col s-12">
      <span class="temp">{{ weather.temp }}&deg;C</span><br />
    </div>
  </div>

  <div class="card-body row">
    <div class="body-desc col s-12">
      <span> {{ weather.description }} </span>
    </div>

    <div class="body-visi-wind col s-12">
      <span title="Tầm nhìn">
        <i class="ri-eye-line"></i>
        {{ weather.visibility }} km
      </span>
      <span title="Tốc độ gió">
        <i class="ri-windy-fill"></i>
        {{ weather.wind }} m/s
      </span>
    </div>

    <div class="body-feelslike col s-6">
      <p>{{ weather.feelsLike }}&deg;C</p>
      <span>Nhiệt độ cảm nhận</span>
    </div>
    <div class="body-humidity col s-6">
      <p>{{ weather.humidity }}%</p>
      <span>Độ ẩm</span>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    weather: {},
  }),
  props: {
    current: Object,
  },
  watch: {
    current: function () {
      this.weather.temp = Math.round(this.current.temp);
      this.weather.description =
        this.current.weather[0].description[0].toUpperCase() +
        this.current.weather[0].description.slice(1);
      this.weather.visibility = parseFloat(this.current.visibility) / 1000;
      this.weather.wind = this.current.wind_speed;
      this.weather.feelsLike = Math.round(this.current.feels_like);
      this.weather.humidity = this.current.humidity;
    },
  },
};
</script>