<template>
  <div id="main" :class="animation.isDay ? 'day' : 'night'">
    <div class="container grid wide">
      <h1 class="app-title">Weather in</h1>

      <div class="card-weather">
        <SearchBar :setData="setData" />
        <Animation :animation="animation" />
        <Current :current="current" />
      </div>
      
      <div class="card-bottom row">
        <Forecast :daily="daily" :isData="isData" />
        <DrawChart :hourly="hourly" :hourlyAir="hourlyAir" :isData="isData" />
        <Air :air="air" :isData="isData" />
      </div>
    </div>
  </div>
</template>

<script>
import Animation from "./Animation.vue";
import Forecast from "./Forecast.vue";
import Current from "./Current.vue";
import DrawChart from "./DrawChart.vue";
import SearchBar from "./SearchBar.vue";
import Air from "./Air.vue";
export default {
  components: {
    SearchBar,
    Animation,
    DrawChart,
    Forecast,
    Current,
    Air,
  },
  data: () => ({
    isData: false,
    hourlyAir: [],
    hourly: [],
    daily: [],
    current: {},
    air: {},
    animation: {},
  }),
  beforeCreate() {
    document.title = "Weather app";
  },
  methods: {
    setData(res) {
      this.isData = true;
      this.current = res[0].current;
      this.animation = {
        isDay: this.current.weather[0].icon.includes("d"),
        id: parseInt(this.current.weather[0].id),
      };
      // set forecast
      this.daily = res[0].daily.slice(1, -1);
      // set air
      this.air = res[1].data;
      // set hourly temp
      this.hourly = res[2].list;
      // set hourly air
      this.hourlyAir = res[3].data;
    },
  },
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
#app {
  font-family: roboto, helvetica, sans-serif;
  text-align: center;
}
#main {
  /* position: fixed; */
  padding-bottom: 20px;
  min-height: 100vh;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #4a5b88;
}

.day {
  background: linear-gradient(to bottom, #5adeff, #f7e59d, #fab244);
  color: #272727;
}

.night {
  background: linear-gradient(to bottom, #141852, #483475, #855988);
  color: white;
}

.card-weather {
  border-radius: 0.5rem;
  position: relative;
  padding: 12px;
  color: white;
  background: linear-gradient(to bottom right, #707070, #2b2f31);
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 20%);
  cursor: default;
  user-select: none;
  overflow: hidden;
}

.app-title {
  font-size: 3rem;
  font-weight: 200;
  font-style: italic;
  padding-bottom: 10px;
}

.card-top .temp {
  font-weight: 300;
  font-size: 4.5rem;
  letter-spacing: -5px;
  white-space: nowrap;
}

.card-top .city-name {
  font-weight: 400;
  font-size: 1.5rem;
}

.card-body {
  padding-bottom: 12px;
  font-size: 1rem;
}

.body-desc {
  padding-bottom: 12px;
}

.body-visi-wind i {
  vertical-align: middle;
  padding-bottom: 3px;
}

.body-visi-wind span + span {
  margin-left: 24px;
}

.body-feelslike,
.body-humidity {
  padding-top: 6px;
}

.body-feelslike p,
.body-humidity p {
  font-size: 2.5rem;
  font-weight: 300;
  letter-spacing: -3px;
}

.card-bottom h3 {
  font-weight: 400;
  padding: 10px 12px;
  text-align: left;
}

.card-bottom h3 i {
  vertical-align: text-top;
  padding-right: 8px;
}

.bottom-daily {
  /* display: flex; */
  /* align-items: center; */
  /* justify-content: center; */
  padding-top: 12px;
  list-style: none;
}

.bottom-daily-list {
  padding: 12px 0;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 20%);
  border-radius: 6px;
}

.daily-rain i {
  vertical-align: text-top;
  padding: 4px;
}

.daily-temp {
  padding-top: 12px;
}

.daily-temp i {
  vertical-align: text-top;
}

.daily-temp span + span {
  margin-left: 12px;
}

.card-top,
.card-bottom,
.card-body,
canvas {
  position: relative;
  z-index: 1;
}
.enable {
  cursor: pointer;
}
.disable {
  cursor: not-allowed;
}
#draw-chart {
  display: flex;
  justify-content: space-between;
}
</style>
