<template>
  <h3 class="col s-12" >
    <span
      :class="isData ? 'enable' : 'disable'"
      @click="isAir = !isAir && isData"
    >
      <i class="ri-flask-line"></i> Chất lượng không khí
    </span>
  </h3>
  <ul class="bottom-air m-12">
    <li
      class="bottom-air-list"
      :style="{ 'background-color': aqi.color }"
      v-if="isAir"
      @click="isDetail = !isDetail"
    >
      <span class="m-2">AQI</span>
      <span class="m-8">{{ description[aqi.description] }}</span>
      <span class="m-2">
        {{ aqi.value }}
        <br />Thang đo <br />BAQI
      </span>
    </li>
  </ul>
  <ul class="bottom-air m-12" v-for="detail in details" :key="detail">
    <li
      class="bottom-air-list"
      :style="{ 'background-color': detail.color }"
      v-if="isDetail && isAir"
    >
      <span class="m-2">
        {{ detail.full_name }}
        <br />
        ({{ detail.name }})
      </span>
      <span class="m-8">{{ detail.description }}</span>
      <span class="m-2">
        {{ detail.value }}
        <br />
        {{ detail.units }}
      </span>
    </li>
  </ul>
</template>

<script>
export default {
  data: () => ({
    country: "",
    isDetail: false,
    isAir: false,
    aqi: {},
    description: {
      "Excellent air quality":
        "Chất lượng không lý lý tưởng cho hầu hết đối tượng; hãy tận hưởng các hoạt động ngoài trời bình thường.",
      "Good air quality":
        "Chất lượng không khí mở mức chấp nhận được đối với hầu hết đối tượng. Tuy nhiên, ở các nhóm đối tượng nhạy cảm có thể sẽ xuất hiện triệu chứng từ nhẹ đến trung bình nếu tiếp xúc quá lâu.",
      "Moderate air quality":
        "Không khí đã đạt mức ô nhiễm cao và không phù hợp với các nhóm đối tượng nhạy cảm. Hãy giảm thời gian ở bên ngoài nếu cơ thể xuất hiện các triệu chứng như khó thở hay ngứa cổ.",
      "Low air quality":
        "Các nhóm đối tượng nhạy cảm sẽ cảm nhận được tác động đến sức khỏe ngay lập tức và nên tránh hoạt động ngoài trời. Các đối tượng khỏe mạnh có thể sẽ gặp tình trạng khó thở và ngứa cổ, hãy cân nhắc việc ở trong nhà và dời lịch cho các hoạt động ngoài trời.",
      "Poor air quality":
        "Mọi tiếp xúc với không khí, dù chỉ vài phút, cũng có thể dẫn đến tác động nghiêm trọng đến sức khỏe đối với mọi đối tượng. Hãy tránh các hoạt động ngoài trời.",
    },
    details: {
      co: {
        full_name: "Carbon monoxide",
        name: "CO",
        description:
          "Thông thường bắt nguồn từ việc đốt cháy nhiên liệu carbon không hoàn toàn, chẳng hạn như nhiên liệu xảy ra trong động cơ ô tô và nhà máy điện. Khi hít phải, khí carbon monoxide có thể ngăn máu vận chuyển oxy. Tiếp xúc có thể gây chóng mặt, buồn nôn và đau đầu. Tiếp xúc với nồng độ quá cao có thể dẫn đến mất ý thức.",
      },
      no2: {
        full_name: "Nitrogen dioxide",
        name: "NO2",
        description:
          "Sinh ra chủ yếu từ các quá trình đốt nhiên liệu, chẳng hạn như các quá trình sử dụng trong công nghiệp và giao thông vận tải. Khi tiếp xúc có thể gây tăng phản ứng phế quản ở bệnh nhân hen suyễn, suy giảm chức năng phổi ở bệnh nhân mắc bệnh phổi tắc nghẽn mãn tính (COPD) và tăng nguy cơ nhiễm trùng đường hô hấp, đặc biệt là ở trẻ nhỏ.",
      },
      o3: {
        full_name: "Ozone",
        name: "O3",
        description:
          "Ozone được tạo ra trong một phản ứng hóa học giữa oxy trong khí quyển, các oxit nitơ, cacbon monoxit và các hợp chất hữu cơ dước tác động của ánh sáng mặt trời. Ozone có thể gây kích ứng đường hô hấp và gây ho, cảm giác nóng rát và khó thở. Ngoài ra, ozone là một trong những thành phần chính của sương mù quang hóa.",
      },
      pm10: {
        full_name: "Bụi mịn",
        name: "PM10",
        description:
          "Các hạt có đường kính dưới 10 micro mét sinh ra từ các quá trình đốt cháy (ví dụ: sưởi ấm trong nhà, cháy rừng), các quá trình cơ học (ví dụ: xây dựng, bụi khoáng, nông nghiệp) và các hạt sinh học (ví dụ: phấn hoa, vi khuẩn, nấm mốc). Chúng có thể tích tụ trong đường dẫn khí. Tiếp xúc trong thời gian ngắn có thể gây kích ứng kích ứng mắt và cổ họng, ho hoặc khó thở cũng như làm trầm trọng thêm bệnh hen suyễn. Tiếp xúc quá mức và thường xuyên có thể dẫn đến nhiều tác động nghiêm trọng đến sức khỏe.",
      },
      pm25: {
        full_name: "Bụi mịn",
        name: "PM2.5",
        description:
          "Các hạt có đường kính dưới 2.5 micro mét sinh ra từ quá trình đốt cháy (ví dụ: nhà máy điện, hệ thống sưởi trong nhà, khói xe, cháy rừng), quá trình cơ học (ví dụ: xây dựng, bụi khoáng) và các hạt sinh học (ví dụ: vi khuẩn, vi rút). Chúng có thể xâm nhập vào phổi và máu. Tiếp xúc trong thời gian ngắn có thể gây kích ứng đường thở, ho và làm trầm trọng thêm các bệnh tim và phổi, biểu hiện như khó thở, đau tim và thậm chí tử vong sớm.",
      },
      so2: {
        full_name: "Sulfur dioxide",
        name: "SO2",
        description:
          "Sinh ra chủ yếu từ quá trình đốt nhiên liệu chứa lưu huỳnh trong công nghiệp, giao thông vận tải và các nhà máy điện. Tiếp xúc sẽ gây kích ứng đường hô hấp, gây ho và tạo ra các phản ứng viêm tại chỗ. Chúng có thể làm trầm trọng thêm các bệnh phổi, ngay cả khi tiếp xúc trong thời gian ngắn.",
      },
    },
  }),
  props: {
    isData: Boolean,
    air: Object,
  },
  watch: {
    air: function () {
      this.aqi.description = this.air.indexes.baqi.category;
      this.aqi.value = this.air.indexes.baqi.aqi;
      this.aqi.color = this.air.indexes.baqi.color + "80";
      for (let i in this.details) {
        this.details[i].color =
          this.air.pollutants[i].aqi_information.baqi.color + "80";
        this.details[i].value = this.air.pollutants[i].concentration.value;
        this.details[i].units = this.air.pollutants[i].concentration.units;
      }
    },
  },
};
</script>

<style>
.bottom-air {
  display: block;
  padding: 5px 0;
}
.bottom-air-list {
  padding: 12px;
  display: flex;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 20%);
  border-radius: 6px;
}
.bottom-air.m-12 {
  cursor: pointer;
}
.bottom-air-list span {
  display: flex;
  align-items: center;
  justify-content: center;
}
.bottom-air-list span .m-8 {
  justify-items: flex-start;
}
.m-8 {
  text-align: justify;
  
}
</style>>